@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@400;700&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap');

:root {
    --primary-main-bg:  hsl(218, 28%, 13%);
    --primary-intro-email-bg: hsl(217, 28%, 15%);
    --primary-footer-bg: hsl(216, 53%, 9%);
    --primary-testimonials-bg: hsl(219, 30%, 18%);
    
    --accent-call-to-action-cyan: hsl(176, 68%, 64%);
    --accent-call-to-action-blue: hsl(198, 60%, 50%);
    --accent-error: hsl(0, 100%, 63%);
    
    --neutral-white: hsl(0, 0%, 100%);
    
    
    
}

body {
    font-size: 14px;
    background: var(--primary-main-bg);
}

.Main {
    min-height: 100%;
}